<template lang="pug">
    .skillGroup
        h2.skillGroup__header {{title}}
        ul.skillGroup__list
            Skill(v-for="item in content" :title="item.name" :percent="item.percent")
</template>
<script>
    const Skill = require('./skill');
    export default {
        components: {
            'Skill': Skill
        },
        props: {
            title: String,
            content: Array
        }
    }
</script>
<style lang="scss" src="./style.scss" scoped></style>


