<template lang="pug">
    div
        Group(v-for="item in groups" :title="item.name" :content="item.content")
</template>
<script>
    const Group = require('./skillGroup');
    export default {

        components: {
            'Group': Group
        },
        data: () => ({
            groups: require('./skills.json')
        })
    }
</script>
<style lang="scss" src="./style.scss" scoped></style>


