<template lang="pug">
  #app
    router-view
</template>

<script>
import { $eventBus } from './main';

export default {
  data: () => ({
    id: 0,
    msg: '',
    text: 'New Text'
  }),
  created() {
    $eventBus.$on('buttonWasClicked', id => {
      this.id = id;
    })
  }
}
</script>

<style lang="scss"></style>



