<template lang="pug">
    ul.menu
        router-link(
        v-for="(menu, index) in menus"
            :key="index"
        tag="li"
            :to="menu.route"
        exact
        ).menu__item
            span.menu__itemLink {{menu.name}}
</template>
<script>
export default {
    data: () => ({
        menus: [
            { name: 'Мои работы', route: '/works' },
            { name: 'Обо мне', route: '/about' },
            { name: 'Блог', route: '/blog' },
        ]
    })
}
</script>

<style lang="scss" src="./style.scss" scoped></style>


