import Vue from 'vue';
import App from './App.vue';
import VueRouter from 'vue-router';
import SvgSprite from 'vue-svg-sprite';

require('normalize.css');
require('./styles/fonts/fonts.scss');
require('./styles/common.scss');

Vue.use(VueRouter);
Vue.use(SvgSprite, {url: require('./assets/svgIcons.svg')});

const WelcomePage = require('./components/_pages/welcome');
const AboutPage = require('./components/_pages/about');
const AdminPage = require('./components/_pages/admin');
const WorksPage = require('./components/_pages/works');
const BlogPage = require('./components/_pages/blog');

const Developer = require('./components/developer');
const Icon = require('./components/icon');
const Socials = require('./components/socials');

function userInfo() {
    return {
        name: 'Artemy Kudriavov'
    }
}

Vue.component('developer', Developer);
Vue.component('appIcon', Icon);
Vue.component('socials', Socials);

console.log(Developer);

const routes = [
    {path: '/', component: WelcomePage, props: userInfo()},
    {path: '/about', component: AboutPage, props: userInfo()},
    {path: '/admin', component: AdminPage, props: userInfo()},
    {path: '/works', component: WorksPage, props: userInfo()},
    {path: '/blog', component: BlogPage, props: userInfo()}
]

const router = new VueRouter({routes, mode: 'history'});

export const $eventBus = new Vue();

new Vue({
    el: '#app',
    router,
    render: h => h(App)
})



