<template lang="pug">
    .wrapper
        .about
            //include ./sections/header/header.pug
            AboutMain.about__main
            section.section.about__map.mapContainer
                .mapContainer__overlay
                    Contacts
                    //include ./sections/footer/footer.pug
                googleMap(name="gmap").mapContainer__googleMap
</template>
<script>

    const Map = require('./map');
    const Contacts = require('./contacts');
    const AboutMain = require('./main');
    export default {
        components: {
            'googleMap': Map,
            'Contacts': Contacts,
            'AboutMain': AboutMain
        }

    }
</script>
<style lang="scss" src="./style.scss" scoped></style>


