<template lang="pug">
    .wrapper
        .welcome
            welcome__header.welcome__header
            section.section.welcome__section
                .welcome__overlayWrapper
                    developer(:name="name").welcome__developer
                    socials.welcome__socials
                welcome__menu.welcome__menu
            welcome__footer(:name="name").welcome__footer

</template>
<script>
    const Footer = require('./footer');
    const Header = require('./header');
    const Menu = require('./menu');

    export default {

        props: {
            name: String
        },
        components: {
            'welcome__header': Header,
            'welcome__menu': Menu,
            'welcome__footer': Footer
        }

    }
</script>
<style lang="scss" src="./style.scss" scoped></style>


