<template lang="pug">
    section.section.about__info.main
        .main__left.info
            h1.info__header Обо мне
            hr.info__line
            .info__photoWrapper
                img.info__photo
            h2.info__whoAmHeader Кто я
            hr.info__line
            .info__infoText
                p.info__infoTextContent Родился на улице Герцена. В гастрономе № 22. Известный экономист. По призванию своему библиотекарь. В народе — колхозник. В магазине — продавец. В экономике, так сказать, необходим.
                p.info__infoTextContent Крепкий учебник будет весомей, чем гастроном на улице Герцена. А на улице Герцена будет расщепленный учебник. Тогда учебник будет проходить через улицу Герцена, через гастроном № 22, и замещаться там по формуле экономического единства.
        .main__right.skills
            h3.skills__header Я бесполезен
            hr.info__line
            .skills__text
                p.skills__textContent Я прохожу курсы по веб-разработке и мобильной разработке. Чтобы совместить приятное с полезным, я предлагаю вам заказать у меня сайт или лэндинг почти за бесплатно. Ваш сайт мне нужен будет для портфолио, от чего ваш сайт только выиграет, ведь мне нужно крутое портфолио :) В этом мне поможет набор бесполезных знаний
            skills(name="skills").skills__container
</template>
<script>
    const Skills = require('./skills');
    export default {
        components: {
            'skills': Skills
        }

    }
</script>
<style lang="scss" src="./style.scss" scoped></style>


