<template lang="pug">
    li
</template>
<script>

    export default {
        props: {
            title: String,
            percent: Number

        },
        mounted: function () {
            class Skill {
                constructor(name, width, height, percent, clBase, clBG) {
                    this.svgns = 'http://www.w3.org/2000/svg';
                    this.svg = document.createElementNS(this.svgns, 'svg');
                    this.svg.classList.add('skill__icon');
                    this.width = width;
                    this.height = height;
                    this.radius = this.width / 2.5;
                    this.percent = percent;
                    this.strokeDasharray = 2 * Math.PI * this.radius;

                    this.svg.setAttribute('width', this.width);
                    this.svg.setAttribute('height', this.height);
                    this.svg.setAttribute('viewBox', `0 0 ${this.width} ${this.height}`);

                    this.baseCircle = this.createCircle(clBase, true);
                    this.bgCircle = this.createCircle(clBG);
                    this.text = this.createText(clBase, name);

                    return this.add();
                }

                createText(color, value) {
                    const text = document.createElementNS(this.svgns, 'text');
                    const x = this.width / 2;
                    const y = this.height / 2;

                    text.setAttribute('x', x);
                    text.setAttribute('y', y);
                    text.setAttribute('font-size', '16px');
                    text.setAttribute('font-family', 'robotomedium');
                    text.setAttribute('text-anchor', 'middle');
                    text.setAttribute('fill', color);
                    text.textContent = value;

                    return text;
                }

                createCircle(color, isBase = false) {
                    const circle = document.createElementNS(this.svgns, 'circle');
                    const cx = this.width / 2;
                    const cy = this.height / 2;

                    circle.setAttribute('cx', cx);
                    circle.setAttribute('cy', cy);
                    circle.setAttribute('r', this.radius);
                    circle.setAttribute('fill', 'none');
                    circle.setAttribute('stroke', color);
                    circle.setAttribute('stroke-width', '25');
                    if (isBase) {
                        circle.setAttribute('transform', `rotate(-90 ${cx} ${cy})`);
                        circle.setAttribute('stroke-dasharray', this.strokeDasharray);
                        circle.setAttribute('stroke-dashoffset', (1 - this.percent) * this.strokeDasharray);
                        circle.setAttribute('fill-opacity', 1 - this.percent);
                    }

                    return circle;
                }

                add() {
                    this.svg.appendChild(this.bgCircle);
                    this.svg.appendChild(this.baseCircle);
                    this.svg.appendChild(this.text);

                    return this.svg;
                }
            }

            const domElement = this.$el;
            const clBase = '#86a77a';
            const clBG = '#dfdcd5';
            const skill = new Skill(this.title, 150, 150, this.percent, clBase, clBG);
            domElement.appendChild(skill);
        }
    }
</script>


